html,
body {
    height: 100%;
}

body {
    background-image: linear-gradient(
        to right,
        #141e30,
        #243b55
    );
    font-size: 14px;
    font-family: "Nokora", sans-serif;
    font-weight: 400;
    font-style: normal;
    color: #38ef7d;
    margin: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

h1 {
    text-align: center;
}

ol,
ul {
    list-style-position: inside;
    margin: 0px;
}

li {
    list-style-type: none;
    margin: 2px;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.status {
    margin-left: 35px;
    margin-bottom: 10px;
    text-align: center;
}

.square {
    background-color: #141e30;
    border-width: 2px;
    border-style: solid;
    border-image: linear-gradient(
        170deg,
        #11998e,
        #38ef7d
    ) 1;
    float: left;
    font-size: 40px;
    font-style: normal;
    font-weight: bold;
    line-height: 47px;
    height: 50px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 20;
    text-align: center;
    width: 50px;
}

.O {
    background: #4158d0;
    background: linear-gradient(
        to top right,
        #4158d0 0%,
        #c85050 45%,
        #ffcc70 90%
    );
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

.X {
    background: #52acff;
    background: linear-gradient(
        to top right,
        #9cecfb 0%,
        #65c7f7 45%,
        #0052d4 90%);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

.history-button {
    -webkit-appearance: none;
    appearance: none;
    width: 100%;
    border: 0;
    margin: 2;
    border-radius: 0;
    background-image: linear-gradient(
        170deg,
        #11998e,
        #38ef7d
    );
}

.square:focus {
    outline: none;
}

.kbd-navigation .square:focus {
    background: #ddd;
}

.game {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
}

.game-info {
    margin-left: 20px;
}

